.post-grid {
  width: 100%;
}
.card {
  border-radius: 5px;
  border: 1px solid var(--light);
  margin: 2rem;
  background-color: var(--highlight);

  header {
    background-color: black;
    padding: 1rem;

    div {
      &.flex {
        gap: 0.5rem;
      }
    }

    span {
      display: inline-block;
    }

    .follow-btn {
      border: 1px solid var(--accent);
      padding: 0.5rem 1rem;
      border-radius: 8px;
      cursor: pointer;
      background-color: unset !important;
      color: var(--accent);

      &:hover,
      &.following {
        background-color: var(--accent) !important;
        color: white;
      }
    }
  }

  .date {
    font-size: 0.6rem;
    display: inline-block;
    width: 100%;
    color: var(--accent);
    text-align: right !important;
    font-family: "Courier New", Courier, monospace;
  }

  .avatar {
    background-color: black;
    border-radius: 50%;
  }

  .username {
    cursor: pointer;
    font-size: 1rem;
    color: white;
    font-weight: 600;
    transition: all 1s ease;

    &:hover {
      color: var(--accent);
    }
  }

  i {
    opacity: 0.8;
    font-size: 1.4rem;
    cursor: pointer;

    &:hover {
      opacity: 1;
    }
  }

  main {
    padding: 1rem;

    img {
      width: 100%;
      aspect-ratio: 2/1;
      object-fit: cover;
      margin: 1rem 0;
      border-radius: 8px;
    }

    .title {
      font-size: 1.4rem;
      margin: 1rem 0;
      color: white;
      font-weight: 600;
      line-height: 1.2;
      transition: all 1s ease;

      &:hover {
        color: var(--accent);
      }
    }

    p {
      color: var(--light);
      margin: 1rem 0;
      line-height: 1.2;
    }
  }

  .like i {
    &.active {
      color: red;
    }
  }

  footer {
    background-color: var(--dark);
    padding: 1rem;
    .footer {
      justify-content: flex-start;
      gap: 2rem;
    }

    i {
      margin-right: 0.5rem;
    }
  }
}

.single-article {
  margin-left: var(--sidebar-width);
  margin-top: 6rem;

  main {
    h3 {
      font-size: 2rem;
      font-weight: 600;
      color: var(--accent);
      margin-bottom: 1rem;
    }

    h2 {
      letter-spacing: 0.5px;
      line-height: 1.5;
    }
  }

  .tags {
    margin: 2rem 0;
    justify-content: flex-start;
    gap: 1rem;

    .navlink {
      opacity: 0.8;
      font-size: 0.8rem;
      text-transform: uppercase;
      letter-spacing: 1px;
      display: inline-block;

      &:hover {
        opacity: 1;
      }
    }
  }
}
